import React, { forwardRef, Fragment, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import InputContainer from '../input-container';
import InputLabel from '../input-label';
import InputBar from '../input-bar';
import InputSelectFocusbox from '../input-select-focusbox';
import InputMessage from '../input-message';
import InputSelect from '../input-select';
import InputOption from '../input-option';
import InputHint from '../input-hint';
import InputText from '../input-text';
import Icon from '../icon';

const Select = forwardRef(({
  ariaLabel,
  autoComplete,
  caretIcon,
  children,
  className,
  css,
  disabled,
  errorIcon,
  errorText,
  helpIcon,
  helpText,
  id = shortid.generate(),
  invalid,
  label,
  onBlur,
  onChange,
  onFocus,
  placeholder,
  required,
  readOnly,
  showErrorText,
  showHelpText,
  ...remainingProps
}, ref) => {
  const [focused, setFocused] = useState();
  const [filled, setFilled] = useState(Boolean(remainingProps.value) || Boolean(remainingProps.defaultValue));
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    setFilled(Boolean(remainingProps.defaultValue) || Boolean(remainingProps.value));
  }, [remainingProps.defaultValue, remainingProps.value]);

  const handleBlur = e => {
    setFocused(false);
    if (typeof onBlur === 'function') {
      onBlur(e);
    }
  };

  const handleChange = e => {
    setFilled(e.target.value !== '');
    if (typeof onChange === 'function') {
      onChange(e);
    }
  };

  const handleFocus = e => {
    setFocused(true);
    if (typeof onFocus === 'function') {
      onFocus(e);
    }
  };

  return (
    <InputContainer
      className={classnames(className)}
      css={css}
      disabled={disabled}
      filled={filled}
      focus={focused}
      readOnly={readOnly}
      invalid={invalid}
    >
      {label ? <InputLabel htmlFor={uid}>{label}</InputLabel> : undefined}
      <InputBar>
        { placeholder ? <InputHint>{placeholder}</InputHint> : undefined}
        { readOnly ?
          <InputText
            aria-describedby={`input-message-help-${uid} input-message-error-${uid}`}
            aria-label={label ? undefined : ariaLabel}
            aria-invalid={invalid}
            autoComplete={autoComplete}
            id={uid}
            disabled={disabled}
            onBlur={handleBlur}
            onFocus={handleFocus}
            readOnly
            required={required}
            ref={ref}
            {...remainingProps}
          />
          :
          <InputSelect
            aria-describedby={`input-message-help-${uid} input-message-error-${uid}`}
            aria-label={label ? undefined : ariaLabel}
            aria-invalid={invalid}
            autoComplete={autoComplete}
            id={uid}
            disabled={disabled}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            readOnly={readOnly}
            ref={ref}
            required={required}
            {...remainingProps}
          >
            {children}
          </InputSelect>
        }
        <InputOption>{caretIcon}</InputOption>
      </InputBar>
      <InputSelectFocusbox />
      <InputMessage
        id={`input-message-help-${uid}`}
        invalid={invalid}
        show
      >
      {
        showHelpText && Boolean(helpText) && focused
          ? <Fragment>{helpIcon} {helpText}</Fragment>
          : ''
      }
      </InputMessage>
      <InputMessage
        id={`input-message-error-${uid}`}
        invalid
        show
      >
        {
          invalid && Boolean(errorText)
            ? <Fragment>{errorIcon} {errorText}</Fragment>
            : ''
        }
      </InputMessage>
    </InputContainer>
  );
});

Select.propTypes = {
  /**
   * Aria Label
   */
  ariaLabel: string,

  /**
   * AutoComplete
   */
  autoComplete: string,

  /**
   * Input Caret Icon
   */
  caretIcon: node,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,


  /**
   * @ignore
   */
  css: func,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Input Message Icon
   */
  errorIcon: node,

  /**
   * Error Text
   */
  errorText: node,

  /**
   * Input Help Message Icon
   */
  helpIcon: node,

  /**
   * Input Help Message
   */
  helpText: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Label
   */
  label: node,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * On Change
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * Hint
   */
  placeholder: node,

  /**
   * ReadOnly
   */
  readOnly: bool,

  /**
    * Required
    */
  required: bool,

  /**
   * Show Error Message
   */
  showErrorText: bool,

  /**
   * Show Help Message
   */
  showHelpText: bool,

  /**
   * Value
   */
  value: string
};

Select.defaultProps = {
  autoComplete: 'off',
  caretIcon: <Icon name="arrow-expand" resolution="tiny" />,
  errorIcon: <Icon name="error" resolution="tiny" />,
  helpIcon: <Icon name="info" resolution="tiny" />
};

Select.displayName = 'Select';

export default Select;
